<template>
    <div>
        <el-dialog title="问题上报" width='96%' top='10' :visible.sync="dialogTableVisible" class="dialog">

          <el-form :model="form" :rules="rules" ref="form">
            <div class='inner'>
              <div class="flex">
                <div class="label">航班号：</div>
                <div class="value">{{form.airlineNo}}</div>
              </div>
              <div class="flex">
                <div class="label">飞机注册号：</div>
                <div class="value">{{form.airRegisterNo}}</div>
              </div>
              <div class="flex">
                <div class="label">机位：</div>
                <div class="value">{{form.seat}}</div>
              </div>
              <div class="flex">
                <div class="label">上报人：</div>
                <div class="value">{{userInfo.username }}</div>
              </div>
              <div class="flex">
                <div class="label">上报时间：</div>
                <div class="value">{{new Date().getFullYear()}}-{{new Date().getMonth()+1}}-{{new Date().getDate()}}&nbsp;{{new Date().getHours()}}:{{new Date().getMinutes()}}</div>
              </div>
              <div class="flex">
                <div class="label"><span style="color:#F56C6C;margin-left: -15px">*</span>上报接收对象：</div>
                <!--<div class="value">{{selectedPersonSTr}}</div>-->
                <div>
                  <el-button type="primary"  @click="choiceSelectFn">选择</el-button>
                </div>
              </div>
              <div class="flex" style="width: 100%">
                <div class="label"  style="visibility: hidden">上报接收对象：</div>
                <!--<div class="value">{{selectedPersonSTr}}</div>-->
                <el-form-item   prop="reportReceive">
                  <div  class="value" >
                    <el-card class="box-card" shadow="never" border-radius="2px">
                      <div>已选择({{ form.reportReceive.length }})：<el-button style="float:right" size="mini" @click="handleClear">清空</el-button></div>
                      <el-scrollbar style="height:120px ">
                        <el-tag :key="tag.id" v-for="(tag,index) in form.reportReceive" closable :disable-transitions="false" @close="handleRemoveUser(tag.userId,index)">
                          {{ tag.userName }}
                        </el-tag>
                      </el-scrollbar>
                    </el-card>
                  </div>
                </el-form-item>
              </div>

              <div class="flex">
                <div class="label">地点：</div>
                <el-form-item>
                  <div class="value"><el-input v-model="form.place" placeholder=""></el-input></div>

                </el-form-item>
              </div>
              <div class="flex">
                <div class="label"><span style="color:#F56C6C;margin: 5px 0 0 -15px">*</span>问题事件性质：</div>
               <el-form-item prop="questionNature">
                 <div class="value">
                   <el-select v-model="form.questionNature" placeholder="请选择">
                     <el-option
                       v-for="item in W_questionNature"
                       :key="item.valCode"
                       :label="item.valData"
                       :value="item.valCode">
                     </el-option>
                   </el-select>
                 </div>
               </el-form-item>
              </div>
              <div class="flex">
                <div class="label">事件描述：</div>
                <el-form-item>
                  <div class="value"><el-input type="textarea" v-model="form.questionRemark" placeholder=""></el-input></div>

                </el-form-item>
              </div>
              <div style="padding-left: 15px" class="upload_image">

                <div class="img"  v-for="(item,index) in imgList" :key="index">
                  <img @click="seeImg(index)" :src="item.filePath" alt="请刷新">
                  <div class="close" @click="closeImg(index)"><img :src="close" alt=""></div>
                </div>
                <div @click="uploadSS" class="photo">+</div>
              </div>

            </div>
          </el-form>
          <div slot="footer" class="bottom">
            <el-button @click='closeFn'>取消</el-button>
            <el-button @click='submitFn("form")' type='templary'>提交</el-button>
          </div>
        </el-dialog>
        <userTree ref="userBox" @onSelected="handleUserSelected"></userTree>
    </div>
</template>
<script>
    import {getUserInfo} from '@/lib/auth.js'
    import request from '@/lib/axios.js'
    import userTree from '@/components/userTree/index';
    import Bridge from '@/assets/js/jsBriage.js'
    import close from '@/assets/img/close.svg'

    export default {
    props: ['airData'],
    components:{
        userTree
    },
    data() {
        return {
          close,
          userInfo:{},
          imgList:[],
            dialogTableVisible: false,
          form:{reportReceive:[],place:'',questionRemark:'',questionNature:''},
          W_questionNature:[],
          rules:{
            reportReceive:[{required:true,message:'请选择接收对象',trigger:'blur'}],
            questionNature:[{required:true,message:'请选择事件性质',trigger:'blur'}],
          },
            dialogImageUrl: '',
              deptList:[],
            selectedPersonSTr:""
        }
    },
    mounted(){
      let _this=this
      Bridge.registerHandler('issueReport',(data)=>{
         this.getImg(data)
       })
      this.getUserInfo()
    },

    methods:{
      closeImg(index){
        this.imgList.splice(index,1);
      },
      seeImg(index){
        let imgPaths=this.imgList.map((k,l)=>k.filePath)
        let data={
          position:index,
          images:imgPaths.join(','),
        }
         Bridge.callHandler('previewImage',JSON.stringify(data) ,(data)=>{

         })
      },
      getImg(data){
         request({
          header:{
            "Content-Type":'application/x-www-form-urlencoded',
          },
          url:`${this.$ip}/mms-file/get-files-by-ids/`,
          method: 'post',
          params:{fileIds:data},
        }).then((d) => {
           if(d.code==200){
             this.imgList.push(...d.data)
          }
        })
      },
        getUserInfo(){
           Bridge.callHandler('userInfo',null,(data)=>{
          this.userInfo= JSON.parse(data)
        })
       },
      uploadSS(){

        Bridge.callHandler('issueReport',null,(data)=>{
          console.log(data);
        })
      },
        handleClear() {
            this.$set(this.form,'reportReceive',[],)
        },
        handleRemoveUser(id,index) {
            this.form.reportReceive.splice(index,1)
        },
        open(workOrder){
            this.form={
                airRegisterNo:workOrder.flightRegisterNo,
                airlineNo:workOrder.flightNo,
                seat:workOrder.seat,
                serialNo:workOrder.serialNo,
                reportReceive:[],
              place:'',questionRemark:'',questionNature:''
            }
            this.dialogTableVisible = true
          request({
            url:`${this.$ip}/mms-parameter/businessDictionaryValue/listByCodes`,
            method: 'post',
            params:{delete:false},
            data:['W_questionNature' ]
          }).then(d => {

            this.W_questionNature=d.data.W_questionNature
          });
        },
        handleRemove(file, fileList) {
           let arrImg=[]
            arrImg=this.form.images.split(',')
          let index=arrImg.indexOf(file.response.data.id)
          arrImg.splice(index,1)
          this.$set(this.form,'images',arrImg.join(','))
        },
      fileChange(file, fileList) {
             if(file.status=='success'){
              let arrImg=[]
              if(this.form.images){
                arrImg=this.form.images.split(',')
              }
              arrImg.push(file.response.data.id)
              this.$set(this.form,'images',arrImg.join(','))
            }
        },
        closeFn(){
          this.form={reportReceive:[]}
            this.dialogTableVisible = false
          this.imgList=[]
        },
        submitFn(form){
          this.$refs[form].validate(valid => {
            if (valid) {
              let data={...this.form}
              request({
                url:`${this.$ip}/mms-workorder/questionReport/save`,
                method: 'post',
                data:data,
              }).then((data) => {
                if(data.code==200){
                  this.closeFn()
                  this.$message({type: 'success', message: '提交成功'});
                }else{
                  this.$message({type: 'error', message: data.message});
                }
              })
            }
          });

        },
        handleUserSelected(selectedPersonList, deptList){
           let userArr=selectedPersonList.map((k,l)=>{
            return {
              userId:k.id,
              userName:k.name,
            }
          })
          this.$set(this.form,'reportReceive',userArr)
            this.selectedPersonSTr = userArr.map(item=>item.userName).join(",")
        },
        choiceSelectFn(){
           let userArr=[]
          if(this.form.reportReceive&&this.form.reportReceive.length){
            userArr= this.form.reportReceive.map((k,l)=>{
              return {
                id:k.userId,
                name:k.userName,
              }
            })
          }
            this.$refs.userBox.open(userArr, '选择推送对象', true);
        },
    }
}
</script>
<style scoped lang='less'>
    .dialog{

     /deep/ .el-dialog__body{
        font-size:20px;
        height:calc(100vh - 200px);
        overflow-y: auto;
       .el-form-item{
         width:calc(100% - 165px);
       }
       .el-tag{
         margin:  5px;
       }
         .el-tag:first-child{
         margin-left:  0px;
       }
      }
      /deep/ .el-dialog__footer{
        text-align: center;
        .el-button:nth-child(2){
          background: #3280e7;
          color: #fff;
        }
      }
        .inner{
          font-size:20px;
            color:#333333;
            line-height: 58px;
            .label{
              padding-left: 15px;
                width:155px;
            }
            .label1{
                width:100%;
            }
            .value1{
                width:100%;
            }
            .value{
                flex:1;
                /deep/ .el-select{
                    width:100%;
                }
            }
            .dialog-footer{
                text-align: center;
            }

            .upload_image{
                margin-top: 10px;

              .photo{
                width: 158px;
                height:159px;
                border: 1px #3ab3ff dashed;
                border-radius: 2px;
                line-height: 159px;
                text-align: center;

                color: #3ab3ff;
                margin: 5px;
                display: inline-block;

               }
              .img {

                display: inline-block;
                width: 159px;
                height:159px;
                vertical-align:top;
                margin: 5px;
                padding: 0;
                position: relative;
                .close{
                  width: 20px;
                  height: 20px;
                  overflow: hidden;
                  position: absolute;
                  border-radius: 100%;
                  top:-10px;
                  right:-10px;
                  img{
                    margin: -2px 0 0 -2px;
                    opcity:1;
                    background: white;
                    border: 0;
                    width: 24px;
                    height: 24px;
                  }
                }
                img{
                  vertical-align:top;
                width: 159px;
                height:159px
              }
              }
            }

        }
    }
</style>



