<template>
	<div class="index">
		<div :class="loading?'zhezhao':'zhezhaoDis '">
			<div>
				<img src="../../../assets/img/loading.svg" alt="">
				<div>数据保存中...</div>
			</div>
		</div>
		<div class='top_header flex justify_between'>
			<div @click="routerPushFn('/orderPatch',{id: id})"><!--{{'<'}}--></div>
			<div>工单签署与管控</div>
			<div></div>
			<div class="iconSvg">
				<icon-svg iconClass="usericon"></icon-svg>
				{{ userName }}{{ workNumber ? `(${workNumber})` : '' }}
			</div>

		</div>
		<div style="height: 68px"></div>

		<div class="page_t">
			<div>工单流水编号-{{ workorder.serialNo }}</div>
			<div>{{ template.airlineCompany }} {{ template.title }}</div>
		</div>
		<div class="base_air">
			<div class="base_air_inner flex_v">
				<div class="lists flex">

					<div class="flex_1"><img :src="airInfo.filePath"></div>
					<div class="flex_1">{{ template.airlineCompany }}</div>
				</div>
				<div class="lists flex">
					<div class="flex_1"><b>{{ template.title }}</b></div>
				</div>
			</div>
		</div>
		<div class="base_item">
			<div class="base_i_inner flex flex_wrap">
				<div class="lists" v-for='(item, index) in baseItemVOList' :key='index'>
					<div class="label" v-show='item.nameCn' style="height:60px">
						<div v-show='item.nameCn'>{{ item.nameCn }}</div>
						<div v-show='item.nameEn'>{{ item.nameEn }}</div>
					</div>
					<!-- 航班号，机型、机号-->
					<div v-if='item.type==1' :id='item.placeholder' class="value1" :key="value5Type[item.value]">
						<!--{{workorder[value5Type[item.value]]}}-->
						<input type="text" v-once :name="item.placeholder" :value="workorder[value5Type[item.value]]"
							   @change="changeReset" :id="item.placeholder">
					</div>
					<!-- 固定值 -->
					<div v-if='item.type==2' class="value2">{{ item.value }}</div>
					<div v-if='item.type==3' class="value3">
						<input type="text" :name="item.placeholder" @change="changeReset" :id="item.placeholder">
					</div>
					<div v-if='item.type==4' class="value4">
						<img :src="item.value.split('$')[1]" style="max-width: 140px;max-height:80px">
					</div>
					<div v-if='item.type==5' class="value5 flex flex_wrap align_center">
						<div v-for="(k,l) in item.value.split(';')" :key="l" class="flex align_center">
							<input type="radio" :id="item.placeholder.split(';')[l]"
								   :name="getRadioName(item.placeholder.split(';')[l])" @change="changeReset"
								   class="Wtui-checkbox">
							<label :for="item.placeholder.split(';')[l]">{{ k }}</label>&nbsp;
							<!-- <div style="width: 100%" >
											  {{'${'+item.placeholder.split(';')[l]+'}'}}
										  </div> -->
						</div>
					</div>
					<!--qian zhang-->
					<div v-if='item.type==6||item.type==8' class="value6" style="position:relative;">
						<!-- <el-button type='primary' @click='signOthFn("sign_"+index)'>签章</el-button> -->
						<button class='sign' @click="signOthMsgBoxFn(item.placeholder, $event,item.type)">签字</button>
						<div style="width:50px;height:50px;position:absolute;left:80px;top:0px">
							<div class="value6_box" :pos="item.placeholder" :id="item.placeholder"></div>
						</div>
					</div>

					<div v-if='item.type==7' class='value7'>
						<input type="date" :name="item.placeholder" @change="changeReset" :id="item.placeholder">
					</div>
				</div>
				<div class="base_i_inner_btn flex justify_around align_center">
					<button @click="saveBasicFn('isActiveSave')"><i v-show='!isActiveSave' class="el-icon-loading"></i>保存
					</button>
					<button @click="BasicUpdateFn('isActiveReset')">
						<i v-show='!isActiveReset' class="el-icon-loading"></i>重置
					</button>
				</div>
			</div>
		</div>
		<div class="instruction" v-if="templateRemarkVO.beforeWarning">
			<p v-html="templateRemarkVO.beforeWarning"></p>
		</div>
		<div class="order_content">
			<!--<div @click="optionAllSign">全签</div>-->
			<div class="order_c_main">
				<div class="item1 flex">
					<div style="width:12%;">
						<div>{{ labelVO.itemLabel }}</div>
						<div>{{ labelVO.itemLabelEnglish }}</div>
					</div>
					<div :style="{width: col==3 ? '73%':col==4?'58%' : '88%'}">
						<div>{{ labelVO.contentLabel }}</div>
						<div>{{ labelVO.contentLabelEnglish }}</div>
					</div>
					<div style="width:15%" v-if='col==3 ||col==4'>
						<div>{{ labelVO.workerLabel }}</div>
						<div>{{ labelVO.workerLabelEnglish }}</div>
					</div>
					<div style="width:15%" v-if='col==4'>
						<div>{{ labelVO.commanderLabel }}</div>
						<div>{{ labelVO.commanderLabelEnglish }}</div>
					</div>
				</div>
				<div class="order_c_b">
					<div v-for='(item) in contentVOListMap' :key='item.id'>

						<div class="flex">
							<div class="item  align_start"
								 style="width:12%;padding:4px;box-sizing:border-box;text-align: center">
								{{ item.reduceIndex }}
								<na-temp v-if="!item.parentId" :id="item.reduceIndex.replace(/\./,'_')+'naTemp'"
										 :serialNumber="item.serialNumber" :newMap="newMap" :item='item'
										 @changeActiveFn='changeActiveFn($event,item)'></na-temp>
								<na-temp v-else :serialNumber="item.serialNumber" :newMap="newMap" :item='item'
										 @changeActiveFn='changeActiveFn($event,item)'></na-temp>

							</div>
							<div class="item2" style="width:88%;"
								 v-if='item.contentDetails&&item.contentDetails.length'>
								<div v-for='itemChild in item.contentDetails' :key='itemChild.key'
									 class="flex itemSignBox">
									<div class="item item1_1"
										 :style="{width: col==3 ? '82.95%':col==2 ? '100%' : '65.9%'}">
										<!--style="width:63.4%;"  v-if="item.reduceIndex.includes('.')" -->

										<div class="textContent textContent11" :contentid="itemChild.id"
											 :class="itemChild.id"
											 v-html='itemChild.content'>

										</div>
										<div class='section_box'
											 style="border-top: 1px solid #d9d9d9;padding: 4px;text-align: right;text-indent: 1em;">
											<el-button @click="itemClear(itemChild, $event)" type='primary' size='mini'>
												清空
											</el-button>
											<el-button @click="saveFn(itemChild, $event)" type='primary' size='mini'>
												保存
											</el-button>
										</div>
										<div class='checkbox_group flex' :class="col==4?'col4B':''">
											<div>
												<input type="checkbox" class="workerLabel"
													   @change='fixCompleteFn(itemChild,$event)'
													   :id='"1"+itemChild.id'/>
												<label :for='"1"+itemChild.id'>{{ labelVO.workerLabel }}已完成</label>
											</div>
											<div v-if='col==4'>
												<input type="checkbox"
													   :class="itemChild.commanderLabel?'commanderComplete1':''"
													   class="commanderComplete"
													   @change='commanderCompleteFn(itemChild,$event)'
													   :id='"2"+itemChild.id'/>
												<label :for='"2"+itemChild.id'>{{ labelVO.commanderLabel }}已完成</label>
											</div>
											<div v-if='!isInvalid&&col==3'>
												<input type="checkbox" class="zuofei"
													   @change='invalidFn(itemChild,$event)' :id='"3"+itemChild.id'>
												<label :for='"3"+itemChild.id'>作废</label>
											</div>
										</div>
										<div class='checkbox_group  col4ZF' v-if='col==4&&!isInvalid'>
											<input type="checkbox" class="zuofei" @change='invalidFn(itemChild,$event)'
												   :id='"3"+itemChild.id'>
											<label :for='"3"+itemChild.id'>作废</label>
										</div>
									</div>
									<div class="item item2_2 itemSign" style="width:17.05%" v-if='col==3 ||col==4'
										 :class="itemChild.workerLabel?'':'duijiao'">
										<div v-if="itemChild.workerLabel">
											<button class='sign'
													@click="showMsgBoxFn(itemChild,'fix_sign_'+itemChild._reduceIndex, $event, 'fixedSignFn',labelVO.workerLabel)">
												签字
											</button>
											<div>{{ getC3Remark(itemChild) }}</div>
											<div style="width:100%;position:absolute;left:0;top:40px;"
												 class="itemSign1">
												<div class="sign_box" :id='"fix_sign_"+itemChild._reduceIndex'
													 :pos='"fix_sign_"+itemChild._reduceIndex'
													 style="width:100%;height:30px;width:100%"></div>
											</div>
										</div>
									</div>
									<div class="item item3_3 itemSign" style="width:17.05%" v-if='col==4'
										 :class="itemChild.commanderLabel?'':'duijiao'">
										<div v-if="itemChild.commanderLabel">

											<button class='sign'
													@click="showMsgBoxFn(itemChild,'travel_sign_'+itemChild._reduceIndex, $event, 'travelSignFn',labelVO.commanderLabel)">
												签字
											</button>
											<div>{{ getC4Remark(itemChild) }}</div>
											<div style="width:100%;position:absolute;left:0;top:40px;"
												 class="itemSign1">
												<div class="sign_box" :id="'travel_sign_'+itemChild._reduceIndex"
													 :pos='"travel_sign_"+itemChild._reduceIndex'
													 style="width:100%;height:30px;width:100%"></div>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div v-else class='flex' style="width:88%;">
								<div class="item" :style="{width: col==3 ? '82.95%':col==2 ? '100%' : '65.9%'}">
									<div class="textContent" contentId="" v-html='item.name'></div>
								</div>
								<div v-if='col==3 ||col==4' class="item" style="width:17.05%"></div>
								<div v-if='col==4' class="item" style="width:17.05%"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>


		<div class="instruction" v-if="templateRemarkVO.afterExplain">
			<p v-html="templateRemarkVO.afterExplain"></p>
		</div>
		<div class="instruction" v-if="templateRemarkVO.afterSchedule">
			<p v-html="templateRemarkVO.afterSchedule"></p>
		</div>

		<div style="height:60px;"></div>
		<div class="bottom_fix    ">
			<div>
				<button @click="questionUploadFn">问题上报</button>
			</div>
			<div>
				<button @click="routerPushFn('/photoUpload',{id})">{{ getbutonName }}</button>
			</div>
			<div>
				<button @click='cleanFn'>清空</button>
			</div>
			<div v-if='!isInvalid'>
				<button @click='backFn'>退回</button>
			</div>
			<div>
				<button @click="submitFn" class="active">提交</button>
			</div>
			<div>
				<button @click="dialogInvalidVisible = true" class="danger">废止</button>
			</div>
		</div>
		<question-upload ref='questionUpload'></question-upload>
		<Reason ref="Reason" @getResult="getResult"></Reason>
		<el-dialog title="废止工单" width="90%" :visible.sync="dialogInvalidVisible">
			<el-form
				label-width="80px"
				:model="invalidForm"
				:rules="{invalidReason:[{ required: true, message: '请输入原因',trigger: 'blur'}]}"
				ref="invalidForm">
				<el-form-item label="原因:" prop="invalidReason">
					<el-input v-model="invalidForm.invalidReason"/>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogInvalidVisible = false">取消</el-button>
				<el-button @click='submitInvalidFormFn("invalidForm")' type='primary'>提交</el-button>
			</div>
		</el-dialog>

	</div>

</template>
<script>

import $ from 'jquery'
import {getUserInfo} from '@/lib/auth.js';
import {bindInputFn, defaultValue} from '@/lib/tools'
import request from '@/lib/axios.js';
import questionUpload from './questionUpload'
import Reason from './reason'
import naTemp from '@/components/naTemp'
import {SignatureInit} from '@/lib/Signature.js'
import postal from 'postal'
import Bridge from '@/assets/js/jsBriage.js'

export default {
	components: {
		questionUpload,
		naTemp, Reason
	},
	data() {
		return {
			userName: getUserInfo().name,
			workNumber: getUserInfo().workNumber,
			loading: false,
			promiseOptions: {},// promise 返回 基础数据 是否验证用户 userIsVerify 和 默认用户名 workUser
			operatorList: [],
			formContent: {},
			resetD: 1,//1 数据无更改；2数据已重置；3数据已修改
			templateRemarkVO: {},
			contentVOList: [],
			workorder: {},
			workerCompleteData: [],
			template: {},
			templateSignObj: {},
			baseItemVOList: [],
			labelVO: {},
			id: '',
			airInfo: {
				'filePath': ''
			},
			value5Type: {},
			isActiveSave: true,
			isActiveReset: true,
			editMap: [], // 用户修改的值
			newMap: [], // 界面更新的值
			oldMap: [], // 用户原有得值
			invalidForm: {
				invalidReason: null
			},
			dialogInvalidVisible: false,
			invalidFormRules: {
				invalidReason: [{required: true, message: '请选择接收对象', trigger: 'blur'}],
			}

		}
	},

	created() {
		if (this.$route.query) {
			this.id = this.$route.query.id
			this.promiseOptions = new Promise((resolve, reject) => {
				request({
					url: `${this.$ip}/mms-parameter/businessDictionaryValue/listByCodes`,
					method: 'post',
					params: {delete: false, valStatus: 1},
					data: ["userIsVerify", 'pwdIsVerify', 'JZ_Signature_workUser', "QW_Signature_workUser", 'QZ_Signature_workUser', 'FCB_Signature_workUser']
				}).then(d => {
					if (d.code == 200) {

						let options = {
							// workUser: d.data.workUser[0] && d.data.workUser[0].valCode,
							workUser: {
								QWJJGD: d.data.QW_Signature_workUser[0] && d.data.QW_Signature_workUser[0].valCode,
								QWSJGD: d.data.QW_Signature_workUser[0] && d.data.QW_Signature_workUser[0].valCode,
								CBGZJLD: d.data.FCB_Signature_workUser[0] && d.data.FCB_Signature_workUser[0].valCode,
								QZGD: d.data.QZ_Signature_workUser[0] && d.data.QZ_Signature_workUser[0].valCode,
								JZ: d.data.JZ_Signature_workUser[0] && d.data.JZ_Signature_workUser[0].valCode,
							},
							userIsVerify: d.data.userIsVerify[0] && d.data.userIsVerify[0].valCode === 'true' ? 'true' : 'false',
							pwdIsVerify: d.data.pwdIsVerify[0] && d.data.pwdIsVerify[0].valCode,
						}
						resolve(options)
					} else {
						reject()
					}
				}).catch(() => {
					reject()
				});
			})
		}

	},
	mounted() {
		let _this = this
		Bridge.registerHandler('pageCommit', (data) => {
			this.$message.success('提交成功')
			_this.init("clean")
		})
		// let _this = this
		this.init("clean", false, true)

		Signature.bind({
			remove: function (fn) {//签章数据撤销时，将回调此方法，需要实现签章数据持久化（保存数据到后台数据库）,
				_this.deleteSignFn(this.getSignatureid(), this.getSignatureData())
				fn(true);//保存成功后必须回调fn(true/false)传入true/false分别表示保存成功和失败

			},
			update: function (fn) {//签章数据有变动时，将回调此方法，需要实现签章数据持久化（保存数据到后台数据库）,执行后必须回调fn(true/false)，传入true/false分别表示保存成功和失败
				fn(true);
			}
		});
		window.postal = postal.subscribe({
			channel: 'websocket_msg',
			topic: 'message',
			callback: async data => {
				if (data.content) {
					if (data.content.subject === "CHANGED") {
						console.log('CHANGED');
						_this.init('clean', true)
					} else if (data.content && data.content.subject === "BACK") {
						this.$alert('工单操作已有变更', '将更新页面', {
							confirmButtonText: '确定',
							callback: action => {
								_this.init('clean')
							}
						})
					} else if (data.content.subject === "INVALID") {
						this.$message.info(data.content.content)
					}
				}
			}
		});
	},
	computed: {
		getRadioName(){
			return (val)=>{
				if(val){
					return val.split('_')[0]
				}
				return val
			}
		},
		getbutonName() {
			return this.workorder && this.workorder.type && this.workorder.type.startsWith("WX") ? '黄页拍照' : '拍照'
		},
		getC3Remark() {
			return (itemChild) => {
				let s = itemChild[this.labelVO.contentLayout == 'C3（三列）' ? 'c3Remark' : 'c4WorkerRemark']
				return s ? `（备注：${s}）` : ''
			}
		},
		getC4Remark() {
			return (itemChild) => {
				let s = itemChild['c4CommanderRemark']
				return s ? `（备注：${s}）` : ''
			}
		},

		contentVOListMap: {
			get: function () {
				const result = [];
				this.contentVOList.forEach((item, index) => {
					const map = (item) => {
						result.push(item)
						if (item.children && item.children.length) {
							item.children.forEach(item1 => {
								result.push(item1)
							})
						}
					}
					map(item)
				})
				let reduceIndex = 0
				let _reduceIndex = 0
				let itemNumber = 0
				let _itemNumber = 0
				result.forEach((item, index) => {
					if (item.number) {
						reduceIndex = 0
						_reduceIndex = 0
						itemNumber = item.number
						_itemNumber = item.serialNumber
						item.reduceIndex = itemNumber
						item._reduceIndex = _itemNumber
					} else {
						reduceIndex++
						item.reduceIndex = itemNumber + '.' + (reduceIndex)
						item._reduceIndex = _itemNumber + '_' + (reduceIndex)
					}
					if (item.contentDetails && item.contentDetails.length) {
						item.contentDetails.forEach((itemChild, indexChild) => {
							itemChild.content = itemChild.content
							itemChild.reduceIndex = item.reduceIndex + '.' + (indexChild + 1)
							itemChild._reduceIndex = item._reduceIndex + '_' + (indexChild + 1)
						})
					}
					//this.$set(item,'active',true)
				})
				return result
			},
			set: function () {
			}
		},
		col: { // 列数
			get: function () {
				let s = ''
				if (this.labelVO.contentLayout == 'R2（二列）') {
					s = '2'
				} else if (this.labelVO.contentLayout == 'C3（三列）') {
					s = '3'
				} else {
					s = '4'
				}
				return s
			},
			set: function () {
			}
		},
		isInvalid: { // 是否显示作废功能
			get: function () {
				return this.workorder.type ? this.workorder.type.startsWith("QW") || this.workorder.type.startsWith("QZ") : false
			},
			set: function () {
			}
		},
		getFXWX() {
			let obj = {}
			this.operatorList.map((k, l) => {
				if (k.userId == getUserInfo().id) {
					obj = {...k}
				}
			})
			return obj.role || ''
		}
	},
	methods: {
		// optionAllSign() {
		// 	let allSignBol = Array.from($(".itemSign1"))
		// 	SignatureInit("maintenance", undefined, this.labelVO.noSignTime)
		// 	let signatureCreator = Signature.create();
		// 	let inputArr = $().find(".textContent").find('input')
		// 	let protectedItems = []
		// 	inputArr.each((index, item) => {
		// 		if ($(item).attr("name").includes("sign")) {
		//
		// 		} else {
		// 			protectedItems.push($(item).attr("id"))
		// 		}
		// 	})
		// 	/* 保护项 */
		// 	protectedItems = protectedItems.filter(i => i)
		// 	let that = this;
		// 	let signparam = {}
		// 	let signPromise = new Promise((resolve, reject) => {
		// 		signatureCreator.handWriteDlg({
		// 			image_height: "1",
		// 			image_width: "3",
		// 			canvas_width: "100",
		// 			canvas_height: "50",
		// 			onBegin: function () {
		// 			},
		// 			onEnd: function () {
		// 			}
		// 		}, function (param) {
		// 			signparam = param
		// 			signatureCreator.runHW(param, {
		// 				offsetX: 1,
		// 				offsetY: 10,
		// 				position: 'fix_sign_7_1_1',
		// 				batchSignature:{
		// 					documentid:'KG2016093001333',//设置文档ID
		// 					documentname:'测试文档KG2016093001',//设置文档名称
		// 					position:"fix_sign_7_1_1",
		// 					protectedItems:[],
		// 				},
		// 				okCall: function (fn) {
		// 					resolve(signparam)
		// 					fn(true);
		// 				},
		// 				cancelCall: function () { //点击取消后的回调方法
		// 				}
		// 			});
		// 		});
		// 	})
		//
		//
		//
		// },
		itemClear(item, e) {
			let s = '此操作将清空本项次所填写的内容以及签名数据'
			if (this.workorder.type.startsWith("WX")) {
				s = '此操作将清空本项次本人所填写的内容以及签名数据'
			}
			if (this.workorder.type.startsWith("WX")) {
				if (this.getFXWX === 'WX') {
					if ($('#2' + item.id).is(':checked')) {
