		// qian zi an niu
		showMsgBoxFn(item, type, $event, fnName, tishi) {
			let _this = this
			if (fnName === 'fixedSignFn') {
				if (!$('#1' + item.id).is(':checked')) {
					this.$message({type: 'warning', message: `请先点击${tishi}已完成`})
					return
				}
			} else if (fnName === 'travelSignFn') {
				if (!$('#2' + item.id).is(':checked')) {
					this.$message({type: 'warning', message: `请先点击${tishi}已完成`})
					return
				}
			}
			// 判断是否是强身份认证还是弱身份认证
			if (this.workorder.type.startsWith("WX")) {  // QZ
				// 强身份
				// http://192.168.4.222:8081/jitGW/random   bluetoothKeyInfo，对的
				let msg = '123'
				Bridge.callHandler('bluetoothKeyInfo', msg, (res) => {
					if (!res) {
						_this.$message({type: 'error', message: '未获取到蓝牙数据，请重试'})
						return
					}
					this.promiseOptions.then((d) => {
						if (d.pwdIsVerify === 'true') {
							_this.$msgBox.showMsgBox({
								isShowPsd: true
							}).then(async (data) => {
								if (data.psd) {
									let judegUser = await _this.findByUserFn({userName: res, password: data.psd})
									if (judegUser == '1') {
										_this[fnName](item, type, $event, res)
									} else {
										_this.$message({type: 'error', message: judegUser});
									}
								}
							})
						} else {
							_this[fnName](item, type, $event, res)
						}
					})

				})
			} else {
				// 弱身份

				this.promiseOptions.then((d) => {
					if (d.userIsVerify === 'false') {
						_this["_" + fnName](item, type, $event, d.workUser[this.workorder.type], null)
						// this.signOthFnR(type, $event, d.workUser)
					} else if (d.userIsVerify === 'true') {
						this.$msgBox.showMsgBox({
							isShowInput: true,
							isShowPsd: true
						}).then(async (data) => {
							if (data.val && data.psd) {
								let judegUser = await this.findByUserFn({userName: data.val, password: data.psd})
								if (judegUser == '1') {
									_this["_" + fnName](item, type, $event, data.val, data.psd)

								} else {
									_this.$message({type: 'error', message: judegUser});
								}
							}
						})
					} else {
						this.$message.error('参数“基础数据 编号userIsVerify”配置错误，请重新配置')
					}
				}).catch(() => {

				})
			}
		},
