		signFn(type, val, psd, id) {
			SignatureInit(val, undefined, this.labelVO.noSignTime)
			var signatureCreator = Signature.create()
			var that = this
			signatureCreator.handWriteDlg({
				image_height: "1",
				image_width: "3",
				canvas_width: "100",
				canvas_height: "50",
				onBegin: function () {
				},
