				}
			}, function (param) {
				let offsetX = 1
				$('.kg-img-div-' + type).each((ind, ele) => {
					offsetX += $(ele).width() + 10
				})
				signatureCreator.runHW(param, {
					offsetX: offsetX,
					// offsetX:1,
					offsetY: 1,
					position: type, //'pos3',//$("#pos3").attr('pos'),//设置盖章定位dom的ID，必须设置
					okCall: function (fn) {//点击确定后的回调方法，this为签章对象 ,签章数据撤销时，将回调此方法，需要实现签章数据持久化（保存数据到后台数据库）,保存成功后必须回调fn(true/false)渲染签章到页面上
						let _this = this
						that.templateSignObj[this.getSignatureid().toString()] = this.getSignatureData()
						fn(true)
						let time = true
						that.loading = true
						let timer = setTimeout(() => {
							if (time) {
								time = false
								that.loading = false
								that.$message.warning('网络错误,请重试')
							}
						}, 10000)

						signatureCreator.getBase64Image(
							this.getSignatureid(),
							this.getSignatureData(),
							type,
							// $($event.target).parents('.item').find(".sign_box").attr("pos"),
							function (fn1, imgdata, signid, sdata) {
								if (time) {
									clearTimeout(timer)
									time = false
									that.postSignFn(
										_this.getSignatureid(), {id},
										type,
										_this.getSignatureid() + '------' + _this.getSignatureData() + '------' + imgdata[1],
										true
									)
								}
							}
						)
						Signature.showAndHideBySignatureId(this.getSignatureid(), false);

					},
					cancelCall: function () {//点击取消后的回调方法
						console.log("取消！")
					}
				})
			})
		},
		findByUserFn(user = {}) {
			if (user.userName && user.password) {
				return new Promise((resolve, reject) => {
					request({
						url: `${this.$ip}/sys/user/findByUser`,
						method: 'get',
						params: {
							user
						}
					}).then((result) => {
						if (result.responseCode === 1000) {
							if (result.data.length > 0) {
								resolve('1')
							} else {
								resolve('密码错误，请重试')
							}
						} else {
							resolve(result.responseMessage)
						}
					})
				})
			}
			// let a = {
			//     "userName":"admin",
			//     "password":"air123456"
			// }
		},
		patchSameMap() {
			console.log('patchSameMap', this.oldMap, this.editMap);
			this.oldMap.map((oldItem, i) => {
				this.editMap.map((editItem, k) => {
					if (oldItem.key === editItem.key) {
						// 有新值进入，旧值未修改， 则新值覆盖旧值
						let a = oldItem.value.toString()
						let b = editItem.value.toString()
						if (oldItem.value.toString() == editItem.value.toString()) {
							if (this.newMap.find(i => i.key == editItem.key)) {
								oldItem.value = this.newMap.find(i => i.key == editItem.key).value.toString()
								editItem.value = this.newMap.find(i => i.key == editItem.key).value.toString()
							}
						}
					}
				})
			})


		},
