		// save sign 保存签章
		postSignFn(signatureid, item, type, getSignatureid, isContent) {
			let s = '/operationInf/sign';
			if (isContent) {
				s = '/operationInf/exceptionSign'
			}
			;
			request({
				url: `${this.$ip}/mms-workorder${s}`,
				method: 'post',
				data: {
					contentDetailId: item.id, //'42b51712b93254d2ede69d6ef251c68c', //item.id,
					serialNo: this.workorder.serialNo,
					key: type,
					value: getSignatureid
				}
			}).then((data) => {
				if (data.code == 200) {
					this.$message({type: 'success', message: '提交成功'})
					Signature.showAndHideBySignatureId(signatureid, true);
				} else {
					$(`div[signatureid=${signatureid}]`).remove()
					this.$message({type: 'error', message: data.message})
				}
				this.loading = false
			}).catch(() => {
				this.loading = false
				$(`div[signatureid=${signatureid}]`).remove()
			})
		},
		judgeSuitableRangeFn(obj) {
			return new Promise((resolve, reject) => {
				if (obj.suitableRange && obj.suitableRange.length) {
					obj.suitableRange.forEach(ele => {
						if (ele.type === obj.type) {
							if (obj.value && !ele.values.includes(obj.value)) {
								this.$confirm(obj.message, '提示', {
									confirmButtonText: '确定',
									cancelButtonText: '取消',
									type: 'warning'
								}).then(() => {
									resolve('1')
								}).catch(() => {
									resolve('0')
								});
							} else {
								resolve('1')
							}
						}
					})
				}
				resolve('1')
			})
		},
		// 维修已完成
		async fixCompleteFn(item, $event) {
			let saveFnRes = await this.saveFn(item, $event, true)
			if (!saveFnRes) {
				$event.target.checked = false
				return false
			}
			let suitableRange = await this.judgeSuitableRangeFn({
				item,
				message: '当前机型不适用，是否继续保存',
				type: '适用机型范围',
				value: this.workorder.airplaneIcao
			})
			if (suitableRange == '0') {
				$event.target.checked = false
				return
			}
			let engineNo = await this.judgeSuitableRangeFn({
				item,
				message: '当前发送机不适用，是否继续保存',
				type: '适用发动机范围',
				value: this.workorder.engineNo
			})
			if (engineNo == '0') {
				$event.target.checked = false
				return
			}
			this.loading = true
			request({
				url: `${this.$ip}/mms-workorder/operationInf/workerComplete`,
				method: 'post',
				data: {
					contentDetailId: item.id, //内容id
					serialNo: this.workorder.serialNo, // 工单流水号
				}
			})
				.then((data) => {
					if (data.code == 200) {
						this.$message({type: 'success', message: '提交成功'})
						//this.workerCompleteData = data.data
					} else if (data.code == 416) {
						this.$message({type: 'warning', message: data.message})
					} else {
						this.$message({type: 'error', message: data.message})
					}
					this.loading = false
					this.init('clean')
				}).catch(() => {
				this.loading = false
				this.init('clean')
			})
		},

