			let eleS = $('.zuofei')
			let e
			for (let i = 0; i < eleS.length; i++) {
				if (eleS.eq(i).is(':checked')) {
					e = true
					i = eleS.length
				}
			}
			if (!e) {
				this.$message({type: 'warning', message: `请先完成至少一项作废`})
				return
			}
			this.$confirm('此操作将清空已作废的项次', '提示', {
