			//至少工作时间
			if (this.template.leastMinutes) {
				let blo = (new Date().getTime() - this.workorder.executeTime < this.template.leastMinutes * 1000 * 60)
				if (blo) {
					this.$message.info(`该工单作业时间少于${this.template.leastMinutes}分钟，暂无法提交！`)
					return false
				}
			}
			/*
			 * 提交之前，保证所有签名栏至少有一个签名 $(".kg-img-div")
			 */
			let allSignBol = Array.from($(".itemSign1")).every(item => {
				return $(item).find('.kg-img-div').length
			})
			// if (this.workorder.type == 'WXGD') {}
			if (!allSignBol) {
				this.$message({type: 'warning', message: '请检查签章是否完成'})
				return
			}
			//桥载判断是否提交黄页拍照
			if (this.workorder.type.startsWith("QZ")) {
				let d = await this.isHYPZ()
				if (d && !d.data) {
					// 未提交黄页拍照
					this.$confirm('未提交盖板照片，确认提交', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
					}).then((d) => {
						// 继续提交工单需要填写原因
						this.$refs.Reason.open(this.workorder.id)
					}).catch(() => {
						this.$message.info('已取消操作')
					})
					return false
				}

			}
			//提交工单
			this.commitOption()

		},
		commitOption() {
			request({
				url: `${this.$ip}/mms-workorder/app/workorder/submit/${this.workorder.id}`,
				method: 'get',
			})
				.then((data) => {
					if (data.code == 200) {
						this.$message({type: 'success', message: '提交成功'})
						setTimeout(() => {
							Bridge.callHandler('finishActivity', null, (res) => {
							})
						}, 260)

					} else {
						this.$message({type: 'error', message: data.message})
					}
				})
		},
		submitInvalidFormFn(form) {
			this.$refs[form].validate(valid => {
				if (valid) {
					request({
						url: `${this.$ip}/mms-workorder/app/workorder/invalid`,
						method: 'get',
						params: {
							id: this.workorder.id,
							invalidReason: this.invalidForm.invalidReason
						}
					})
						.then((data) => {
							if (data.code == 200) {
								this.$message({type: 'success', message: '提交成功'})
								this.dialogInvalidVisible = false
								// this.$router.push({path: '/orderPatch', query: {id: this.workorder.id}})
								this.$router.go(-1);//返回上一层
							} else {
								this.$message({type: 'error', message: data.message})
							}
						})
				}
			});
		},
		questionUploadFn() {
			this.$refs.questionUpload.open(this.workorder)
		},
		routerPushFn(path, query = {}) {
			let objS = JSON.stringify({workOrderId: this.id, pageFile: this.workorder.pageFile})
			Bridge.callHandler('yellowPage', objS, (res) => {
				console.log(res, 111, 2,);
			})
			// this.$router.push({ path,query})
		},

		saveActive(obj) {
			return request({
				url: `${this.$ip}/mms-workorder/operationInf/active`,
				method: 'post',
				data: {
					...obj
				}
			})
		},
		async changeActiveFn(e, item) {
			// contentDetailId 项次ID
			let obj = {
				contentDetailId: item.id,
				serialNo: this.workorder.serialNo,
				value: e.value.toString(),
				key: e.key + '_' + item.serialNumber,
			}
			let data = await this.saveActive(obj)
			// 当前点击图标激活时，需要重置该项次其他激活图标
			if (data && data.code == 200 && data.data.value == 'true') {
				let arr1 = [['notApplicable', 'na'], ['cycle', 'cycle'], ['hook', 'gou']]
				let index = ''
				arr1.map((k, l) => {
					// 筛选显示的图标
					if (item[k[0]]) {
						// 得到非当前点击图标
						if (e.key != k[1]) {
							index = l
						}
					}
				})
				let blo = false
				// 查看是否存在 其他图标 并且已激活
				index !== '' && this.newMap.map((k, l) => {
					if (k.key === arr1[index][1] + '_' + item.serialNumber && k.value === 'true') {
						blo = true
						return
					}
				})
				if (blo) {
					// 为true需要重置为false
					obj.value = 'false'
					obj.key = arr1[index][1] + '_' + item.serialNumber
					this.saveActive(obj).then((data1) => {
						if (data1.code == 200) {
							this.$message({type: 'success', message: '修改成功'})
							this.getBySerialNoFn('clean')
						} else {
							this.$message({type: 'error', message: data1.message})
						}
					})
					return false
				}

			}
			if (data.code == 200) {
				this.$message({type: 'success', message: '修改成功'})
				this.getBySerialNoFn('clean')
			} else {
				this.$message({type: 'error', message: data.message})
			}

		},

		inputVerify(textArr, type, blo) {
			// let textArr = $(".base_i_inner").find("input[type='text']");
			let res = true
			let obj = {}
			textArr.each((index, ele) => {
				if (type == 'text' && !$(ele).val()) {
					blo && this.$message.warning('有输入框为空，请检查')
					res = false
					return false
				} else if (type == 'checkbox' && !ele.checked) {
					res = false
					return false
				} else if (type == 'radio') {
					obj[$(ele).attr('name')] = ele.checked || obj[$(ele).attr('name')]
				}
			})
			if (type == 'radio') {
				for (let k in obj) {
					if (!obj[k]) {
						res = false
						break
					}
				}
			}
			return res
		},
		saveBasicFn(type, blo) {
			if (!this[type]) {
				return false
			}
			this[type] = false
			let map = {}
			if (type === 'isActiveSave') {
				let textArr = $(".base_i_inner").find("input[type='text']");
				textArr.each((index, ele) => {
					map[$(ele).attr("name")] = $(ele).val()
				})
				let checkBoxArr = $(".base_i_inner").find("input[type='checkbox']")
				checkBoxArr.each((index, ele) => {
					map[$(ele).attr("name")] = $(ele).is(':checked')
				})
				let radioBoxArr = $(".base_i_inner").find("input[type='radio']")
				radioBoxArr.each((index, ele) => {
					map[$(ele).attr("id")] = $(ele).is(':checked')
				})
				let dateArr = $(".base_i_inner").find("input[type='date']")
				dateArr.each((index, ele) => {
					map[$(ele).attr("name")] = $(ele).val()
				})

			} else {
				let textArr = $(".base_i_inner").find("input[type='text']");
				textArr.each((index, ele) => {
					map[$(ele).attr("name")] = ''
				})
				let radioBoxArr = $(".base_i_inner").find("input[type='radio']")
				radioBoxArr.each((index, ele) => {
					map[$(ele).attr("id")] = false
				})
				let checkBoxArr = $(".base_i_inner").find("input[type='checkbox']")
				checkBoxArr.each((index, ele) => {
					map[$(ele).attr("name")] = false
				})
				let dateArr = $(".base_i_inner").find("input[type='date']")
				dateArr.each((index, ele) => {
					map[$(ele).attr("name")] = $(ele).val()
				})
			}
			this.loading = true
			request({
				url: `${this.$ip}/mms-workorder/operationInf/saveBasic`,
				method: 'post',
				data: {
					serialNo: this.workorder.serialNo,  // 工单流水号
					map
				}
			})
				.then((data) => {
					if (!blo) {
						setTimeout(() => {
							if (data.code == 200) {
								this.resetD = 1
								this.$message({type: 'success', message: '保存成功'})
							} else {
								this.$message({type: 'error', message: data.message})
							}

							this.init('clean')
						}, 500)
					}
					this[type] = true
					this.loading = false
				}).catch(res => {
				if (!blo) {
					this.$message({type: 'error', message: res})
					this.init('clean')
				}
				this.loading = false
				this.isActiveSave = true
			})
		},
		BasicUpdateFn(type) {
			if (this.resetD == 1) {
				this.$message.info('数据未更改，无需此操作')
			} else if (this.resetD == 2) {
				this.$message.info('数据已重置，请勿重复操作')
			} else {
				this['isActiveReset'] = false
				this.init('clean', true)
			}

		},
		signBasicFn(signatureid, obj) {
			request({
				url: `${this.$ip}/mms-workorder/operationInf/signBasic`,
				method: 'post',
				data: {
					serialNo: this.workorder.serialNo,  // 工单流水号
					...obj
				}
			}).then((data) => {
				if (data.code == 200) {
					this.$message({type: 'success', message: '保存成功'})
					Signature.showAndHideBySignatureId(signatureid, true);

				} else {
					$(`div[signatureid=${signatureid}]`).remove()

					this.$message({type: 'error', message: data.message})
				}
				this.loading = false
			}).catch(() => {
				this.loading = false
				$(`div[signatureid=${signatureid}]`).remove()

			})
		},
		getFileByIdFn(id) {
			request({
				url: `${this.$ip}/mms-file/get-file-by-id/${id}`,
				method: 'get',
			})
				.then((data) => {
					if (data.code == 200) {
						this.airInfo['filePath'] = data.data.filePath
						//this.$message({type: 'success', message: '保存成功'})
					} else {
						//this.$message({type: 'error', message: data.message})
					}
				})
		},
		signBasicActiveFn() {
			return Array.from($(".commanderComplete1")).every(i => $(i).is(":checked"))
		},
		deleteSignFn(getSignatureid, getSignatureData) {
			let key = $(`div[signatureid='${getSignatureid}']`).attr("elemid")
			let contentDetailId = ''
			let value = getSignatureid
			request({
				url: `${this.$ip}/mms-workorder/operationInf/cancelSign`,
				method: 'post',
				data: {
					serialNo: this.workorder.serialNo,
					key,
					contentDetailId,
					value
				}
			})
				.then((data) => {
					if (data.code == 200) {
						//this.$message({type: 'success', message: '保存成功'})
					} else {
						//this.$message({type: 'error', message: data.message})
					}
				})
		},
		removeSignatureFn(signatureid) {
			if (signatureid) {
				$(`[signatureid=${signatureid}]`)[0].remove();
			} else {
				$(".kg-img-div").each((index, ele) => {
					//$(`[signatureid=${signatureid}]`)[0].remove();
					$(ele)[0].remove()
					//signatureCreator.removeSignature('KG2016093001333', $(ele).attr("signatureid"));
				})
			}
		},
		async saveFn(itemChild, $event, blo) {
			let imgcycle = $($event.target).parents('.item2').siblings('.align_start').find('img[type=quan_true]')
			let imggou = $($event.target).parents('.item2').siblings('.align_start').find('img[type=gou_true]')
			let imgNa = $($event.target).parents('.item2').siblings('.align_start').find('img[type=na_true]')

			let textContentele = $($event.target).parents('.section_box').siblings('.textContent')
			if (blo) {
				textContentele = $($event.target).parents('.checkbox_group').siblings('.textContent')
			}
			// 激活gou 或者cycle 所有项必填	  input必须校验
			let text = this.inputVerify(textContentele.find("input[name*='input']"), 'text', true)
			if (!text) {
				return
			}
			let Dimgcycle = [], Dimggou = [];
			if (!imgcycle.length && !imggou.length && !imgNa.length) {
				Dimgcycle = $(`#${itemChild._reduceIndex.split('_')[0] + "naTemp"}`).find('img[type=qaun_true]')
				Dimggou = $(`#${itemChild._reduceIndex.split('_')[0] + "naTemp"}`).find('img[type=gou_true]')
			}
			console.log(Dimgcycle, Dimggou);
			if (imgcycle.length || imggou.length || Dimgcycle.length || Dimggou.length) {
				let checkbox = this.inputVerify(textContentele.find("input[type*='checkbox']"), 'checkbox')
				let radio = this.inputVerify(textContentele.find("input[type*='radio']"), 'radio')
				if (!checkbox || !radio) {
					this.$message.warning('该项次为必填项，存在未完成部分,请检查')
					return
				}
			}

			let suitableRange = await this.judgeSuitableRangeFn({
				itemChild,
				message: '当前机型不适用，是否继续保存',
				type: '适用机型范围',
				value: this.workorder.airplaneIcao
			})
			if (suitableRange == '0') {
				this.init(true)
				return
			}
			let engineNo = await this.judgeSuitableRangeFn({
				itemChild,
				message: '当前发送机不适用，是否继续保存',
				type: '适用发动机范围',
				value: this.workorder.engineNo
			})
			if (engineNo == '0') {
				this.init(true)
				return
			}
			let map = {}
			if (textContentele.find("input")) {
				let inputArr = textContentele.find("input[name*='checkBox']")
				for (let i = 0; i < inputArr.length; i++) {
					map[inputArr.eq(i).attr("name")] = inputArr.eq(i).is(':checked')
				}
				let RchaArr = textContentele.find("input[name*='ycha']")
				for (let i = 0; i < RchaArr.length; i++) {
					map[RchaArr.eq(i).attr("id")] = RchaArr.eq(i).is(':checked')
				}
				let radioArr = textContentele.find("input[name*='radio']")
				for (let i = 0; i < radioArr.length; i++) {
					map[radioArr.eq(i).attr("id")] = radioArr.eq(i).is(':checked')
				}
				let CchaArr = textContentele.find("input[name*='fcha']")
				for (let i = 0; i < CchaArr.length; i++) {
					map[CchaArr.eq(i).attr("name")] = CchaArr.eq(i).is(':checked')
				}

				// let inputRadioArr = textContentele.find("input[name*='radio']")
				// for (let i = 0; i < inputRadioArr.length; i++) {
				//   map[inputRadioArr.eq(i).attr("name")] = inputRadioArr.eq(i).is(':checked')
				// }

				let inputNa = textContentele.find("input[name*='NA']")
				for (let i = 0; i < inputNa.length; i++) {
					map[inputNa.eq(i).attr("name")] = inputNa.eq(i).is(':checked')
				}
				let inputText = textContentele.find("input[name*='input']")
				for (let i = 0; i < inputText.length; i++) {
					map[inputText.eq(i).attr("name")] = inputText.eq(i).val()
				}
			}
			const deepMap = []
			if (Object.keys(map).length > 0) { // 判断是否有值
				Object.keys(map).forEach(item => {
					if (this.editMap.find(i => i.key == item)) { // 判断是否有版本
						deepMap.push({
							key: item,
							value: Reflect.get(map, item),
						})
					} else {
						deepMap.push({
							key: item,
							value: Reflect.get(map, item),
						})
					}
				})
			}
			console.log(deepMap)
			this.loading = true
			return request({
				url: `${this.$ip}/mms-workorder/operationInf/save`,
				method: 'post',
				data: {
					dataList: deepMap,
					contentDetailId: itemChild.id,
					serialNo: this.workorder.serialNo
				}
			})
				.then((data) => {
					this.loading = false

					if (data.code == 200 && data.data) {
						this.$message({type: 'success', message: '保存成功'})
						// 点击提交过后，修改后的数据将变成旧数据
						this.oldMap = [] //JSON.psrse(JSON.stringify(this.editMap))
						this.editMap = []
						return true
					} else {
						this.$message({type: 'warning', message: data.message})
						return false
					}
				}).catch(() => {
					this.loading = false
				})
		}
	}
}

