				.textContent {
					text-align: left;
					position: relative;
					padding: 10px 0;

					/deep/ table {
						width: 100% !important;
					}

					// position: absolute;
					// left:0;
					// top:0;
					// height: 50px;
				}
			}
		}

		.table {
			width: 100%;

			thead {
				tr {
					th {
						padding: 5px;
						text-align: center;
					}
				}
			}

			tbody {
				tr {
					td {
						text-align: center;
						padding: 10px 0;
						vertical-align: top;

						.sign {
							width: 70px;
							height: 25px;
							line-height: 25px;
							border: 1px solid #333;
							background: #3280e7;
							color: #fff;
							font-size: 14px;
						}
					}
				}
			}

			.checkbox_group {
				font-size: 16px;
				height: 40px;
				line-height: 40px;
				border-top: 1px solid #979797;
				border-bottom: 1px solid #979797;

				&:nth-last-child(1) {
					border-bottom: none;
				}

				input[type='checkbox'] {
					width: 24px;
					height: 24px;
					margin: 0 6px;
				}
			}
		}
	}

	.page_t {
		line-height: 40px;
		font-size: 24px;
	}

	.bottom_fix {
		position: fixed;
		left: 0;
		bottom: 0px;
		z-index: 100;
		background: #fff;
		height: 60px;
		line-height: 60px;
		width: 100%;
		border-top: 1px solid #979797;
		display: flex;
		justify-content: space-evenly;

		button {
			width: 90px;
			height: 44px;
			line-height: 44px;
			outline: none;
			border: 1px solid #979797;
			font-size: 16px;
			color: #222222;
			letter-spacing: 2px;
			border-radius: 3px;
		}

		.active {
			background: #3280e7;
			color: #fff;
		}

		.danger {
			background: #e93670;
			color: #fff;
		}
	}
}

.duijiao {
	background: linear-gradient(to top right,
	rgba(0, 0, 0, 0) 0%,
	rgba(0, 0, 0, 0) calc(50% - 1.5px),
	rgba(0, 0, 0, 0) 50%,
	rgba(0, 0, 0, 0) calc(50% + 1.5px),
	rgba(0, 0, 0, 0) 100%), linear-gradient(to bottom right,
	rgba(0, 0, 0, 0) 0%,
	rgba(0, 0, 0, 0) calc(50% - 1.5px),
	rgba(0, 0, 0, 1) 50%,
	rgba(0, 0, 0, 0) calc(50% + 1.5px),
	rgba(0, 0, 0, 0) 100%) !important;
}

.instruction {
	padding: 15px 0;
}

.col4ZF {
	text-align: left;
	border: none !important;
}

.col4B {
	border-bottom: none !important;
}

/deep/ .Wtui-checkbox_zc:checked,
/deep/ .Wtui-radioCC:checked,
/deep/ .Wtui-checkbox:checked {
	background: #1673ff;
	border: solid 0px #888888;
	position: relative;
}

/deep/ .Wtui-checkbox_zc:focus,
/deep/ .Wtui-radioCC:focus,
/deep/ .Wtui-checkbox:focus {
	-webkit-appearance: none;
	-webkit-user-select: none;
	outline: none;
}

/deep/ .Wtui-checkbox_zc:disabled,
/deep/ .Wtui-checkbox:disabled {
	background: rgba(127, 127, 127, 0.5);
}

/deep/ .Wtui-checkbox_zc,
/deep/ .Wtui-checkbox,
/deep/ .Wtui-radioCC {
	width: 13px;
	height: 13px;
	background-color: #ffffff;
	border: solid 1px #888888;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	padding: 0;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	cursor: default;
	-webkit-appearance: none;
	-webkit-user-select: none;
	user-select: none;
	-webkit-transition: background-color ease 0.1s;
	transition: background-color ease 0.1s;
}

/deep/ .Wtui-checkbox_zc:checked::after,
/deep/ .Wtui-checkbox:checked::after {
	content: '';
	top: 2px;
	left: 2px;
	position: absolute;
	background: transparent;
	border: #fff solid 2px !important;
	border-top: none !important;
	border-right: none !important;
	height: 3px;
	width: 7px;
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

/deep/ .Wtui-radioCC:checked::after {
	content: '×';
	/*left: calc(50% - 4px);*/
	left: 50%;
	/*top: calc(50% - 8px);*/
	top: 50%;
	line-height: 5px;
	transform: translate(-50%, -50%);
	font-size: 14px;
	position: absolute;
	color: #fff;
	font-weight: bold;

	border-top: none;
	border-right: none;

}

/deep/ .Wtui-checkboxCC {
	-webkit-border-radius: 2px;
	border-radius: 2px;
}

/deep/ .Wtui-checkbox_zc {
	-webkit-border-radius: 2px;
	border-radius: 2px;
}

</style>



