<template>
	<div>
		<el-dialog title="填写不上传原因" width="90%" :before-close="close" :visible.sync="dialogInvalidVisible">
			<el-form
				label-width="80px"
				:model="form"
				:rules="{reason:[{ required: true, message: '请输入原因',trigger: 'blur'}]}"
				ref="invalidForm">
				<el-form-item label="原因:" prop="reason">
					<el-input v-model="form.reason" type="textarea" placeholder="请输入原因"/>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="close">取消</el-button>
				<el-button @click='submitInvalidFormFn("invalidForm")' type='primary'>提交</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import request from '@/lib/axios.js';

	export default {
        name: "reason",
        components: {},
        data() {
            return {
            	data:{},
				form:{},
				dialogInvalidVisible:false
			}
        },
        methods: {
        	close(){
        		this.dialogInvalidVisible=false
				this.$emit('getResult',false)
			},
			open(data){
        		this.data=data
				this.dialogInvalidVisible=true
			},
			submitInvalidFormFn(form){

				this.$refs[form].validate(valid=>{
					if(valid){
						 request({
							url: `${this.$ip}/mms-workorder/app/workorder/reason`,
							method: 'get',
							 params:{
								id:this.data,
								 reason:this.form.reason
							 }
						}).then((d)=>{
							if(d.code==200){
								this.close()
								this.$emit('getResult',d.data)
							}else{
								this.$message.info('保存原因失败')
							}
 						 }).catch(()=>{
							 // this.$message.info('保存原因失败')
						 })
					}
				})

			}
		},

    }
</script>

<style lang="less" scoped>
.dialog-footer{
	/*text-align: center;*/
}
</style>


