<template>
	<div>
		<div v-if="item.notApplicable ">
			<img :src="na" v-if="isActive('na')" alt="" @click="changeActiveFn('na',true,'na',0)" style="width:34px">
			<img :src="na_active" type="na_true" v-else @click="changeActiveFn('na_active',false,'na',0)" alt="" style="width:34px">
		</div>
		<div v-if="item.cycle ">
			<img :src="quan" v-if="isActive('cycle') " alt="" @click="changeActiveFn('quan',true,'cycle',1)" style="width:34px">
			<img :src="quan_active" type="quan_true" v-else alt="" @click="changeActiveFn('quan_active',false,'cycle',1)" style="width:34px">
		</div>
		<div v-if="item.hook ">
			<img :src="gou"   v-if="isActive('gou')" alt="" @click="changeActiveFn('gou',true,'gou',2)" style="width:34px">
			<img :src="gou_active" type="gou_true" v-else alt="" @click="changeActiveFn('gou_active',false,'gou',2)" style="width:34px">
		</div>

	</div>
</template>

<script>
	import gou from '@/assets/img/gou.svg'
	import gou_active from '@/assets/img/gou_active.svg'
	import quan from '@/assets/img/quanUncheck.svg'
	import quan_active from '@/assets/img/quanCheck.svg'
	import na from '@/assets/img/na.svg'
	import na_active from '@/assets/img/na_active.svg'

	export default {
		props: {
			width: {
				type: String,
				default: "34"
			},
			item: {
				type: Object,
				default: {}
			},
			newMap: {
				type: Array,
			},
			serialNumber: {
				type: String,
			}
		},
		data() {
			return {
				quan_active,
				quan,
				gou,
				gou_active,
				na,
				na_active
			}
		},
		computed: {
			isActive() {
				return (s) => {
					let blo = true;//默认灰色图标
					for (let i = 0; i < this.newMap.length; i++) {
						if (this.newMap[i].key == s + '_' + this.serialNumber && this.newMap[i].value === 'true') {
							blo = false
							break
						}
					}
					return blo
				}
			}
		},
		methods: {
			changeActiveFn(s, v, kn,index) {
				this.$emit('changeActiveFn', {name: s, value: v, key: kn,index:index+''})
			}
		}


	};
</script>

<style scoped lang="less">

</style>


